/* Style the buttons inside the tab */

.emailbtn,
.menu1 button,
.menu2 button {
	background-color: inherit;
	color: $menu1-txt;
	float: left;
	outline: none;
	cursor: pointer;
	/*transition: all 0.4s ease 0s;*/
	font-size: 17px;
	border: none;
	border-right-width: 1px;
	border-right-style: solid;
	border-right-color: #CC3;
	padding-top: 12px;
	padding-right: 12px;
	padding-bottom: 12px;
	padding-left: 12px;
}

button.emailbtn,
 {
	float: right !important;
	padding-right: 8px;
	padding-left: 8px;
}

.menu2 button.emailbtn	{
	color: $menu2-txt;
	padding-bottom: 12px;
}

.emailbtn:hover,
.menu1 button:hover,
.b1.activ		{
	background-color: $menu1-hvr;
}

.emailbtn:hover,
.menu2 button:hover, .b2.activ,
.b3.activ, .b4.activ
	{
