	color: $mob-menu-link-tx;
	padding-left: 26%;
	font-size: 20px;
	font-weight:bold;
}


.grid-GD1 {
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: auto;
	margin: 0 auto;
	max-width: 599px;
	grid-column-gap: 0px;
	grid-row-gap: 0px;
}

.grid-element {
	background-color: $panel-default-bg;
	color: $panel-default-tx;
	padding: 10px;
	font-size: 1em;
	margin: 0px;
	border: 1px solid $element-border;
	border-collapse:collapse;
}

.ge-nopad {
	background-color: $panel-default-bg;
	color: $panel-default-tx;
	padding: 0px;
	font-size: 1em;
	margin-top: 1px;
	margin-right: 0px;
	margin-bottom: 1px;
	margin-left: 0px;
}
.header, header2 {
	grid-column-start: 1;
	grid-column-end: 2;
	grid-row-start: 1;
	grid-row-end: 2;
 	background-color: $hdr-bg;
	color: $hdr-tx;
}

.searchbar {
	grid-column-start: 1;
	grid-column-end: 2;
	grid-row-start: 2;
	grid-row-end: 3;
	background-color: pink;
	color: $hdr-tx;
	z-index: 10;
}

.menubar, .menubar2, .menubar3 {
	grid-column-start: 1;
	grid-column-end: 2;
	grid-row-start: 3;
	grid-row-end: 4;
	background-color: $menu1-bg;
	color: $menu1-txt;
	display: block;
	z-index: 100;
}

.greeting {
	grid-column-start: 1;
	grid-column-end: 2;
	grid-row-start: 4;
	grid-row-end: 5;
	background-color: $grtg1-bg; /* stupid */
	color: $grtg1-tx;
	z-index: 10;
}

.smenubar, .smenubar2, .smenubar3 {
	grid-column-start: 1;
	grid-column-end: 2;
	grid-row-start: 5;
	grid-row-end: 6;
	background-color: $menu2-bg;
	color: $menu2-txt;
	z-index: 100;
	display:none;
}

.greeting2 {
	grid-column-start: 1;
	grid-column-end: 2;
	grid-row-start: 6;
	grid-row-end: 7;
	background: $grtg2-bg;
	color: $grtg2-tx;
	z-index: 10;
}

.sidebar1 {
	grid-column-start: 1;
	grid-column-end: 2;
	grid-row-start: 7;
	grid-row-end: 8;
